export const frontmatter: StaticPageFrontmatter = {
  title: 'Bonzo Migrates to Telgorithm and Reduces Customer Churn',
  metaTitle: 'Bonzo Migrates to Telgorithm and Reduces Customer Churn',
  excerpt:
    'With customers such as LendingTree, Zillow, Realtor.com, Bankrate and more, Bonzo is one of the top CRM and communication providers for the mortgage industry. Within their CRM, Bonzo offers omni channel communications for voice, email and SMS. For Bonzo, working with the right partner in the 10DLC space was crucial to growing their SMS business and keeping loyal customers.',
  slug: '/customer-stories/bonzo/',
};
